import { useSelector, useDispatch } from 'react-redux'
import { RootState } from '@reducers/index'
import { increase, decrease, increaseBy } from '@reducers/counter'
import Counter from '@components/TestCounter/Counter'

function CounterContainer() {
  const count = useSelector((state: RootState) => state.counter.count)
  const dispatch = useDispatch()

  const onIncrease = () => {
    dispatch(increase())
  }

  const onDecrease = () => {
    dispatch(decrease())
  }

  const onIncreaseBy = (diff: number) => {
    dispatch(increaseBy(diff))
  }

  return <Counter count={count} onIncrease={onIncrease} onDecrease={onDecrease} onIncreaseBy={onIncreaseBy} />
}

export default CounterContainer
